import React, { FC } from "react";

interface StatusBarProps {
  width: number;
  height: number;
  colorDepth: string;
}

const StatusBar: FC<StatusBarProps> = ({ width, height, colorDepth }) => {
  return (
    <div className="status-bar">
      : {width}{height}px &nbsp;|&nbsp;  : {colorDepth}
    </div>
  );
};

export default StatusBar;
